@font-face {
  font-family: 'Dolce Vita Heavy A Few Extra Pounds';
  font-style: normal;
  font-weight: normal;
  src: local('Dolce Vita Heavy A Few Extra Pounds'), url('dolce vita heavy bold.woff') format('woff');
  }
html{
  scroll-behavior: smooth;
}
.App {
  text-align: center;
}

.App .navbar-brand {
  font-weight: bold;
}
.App-header {
  background-color: #282c34;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  /* font-size: calc(10px + 2vmin); */
  color: white;
}
.App-header h1{
  font-family:'Dolce Vita Heavy A Few Extra Pounds';
    font-weight:bold;
}
.row
{
  width: 100%;
  margin-left: 0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
input[type=text]{
  height:50px;
  background-color: #282c34;
  color: firebrick;
  border: none;
  margin: 0;
  padding: 0;
  font-size:calc(4px + 2vmin); 
  width: 66.669%;
  box-sizing: border-box;
  padding: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.button{
  color: White;
  height: 50px;
  width: 33.33%;
  background-color: rgba(148, 34, 34, 0.849);
  border: none;
  transition: 0.5s;
  font-family:'Dolce Vita Heavy A Few Extra Pounds';
  font-size:calc(4px + 2vmin);
  font-weight: bold;
  float: left;
  
}

.half{
  min-width: 50%;
}
.button:hover,.active{
  background-color: white;
  color: #282c34;
  cursor: pointer;

}
.button2{
  color: White;
  height: 50px;
  background-color:#282c34 ;
  width: 100%;
  border: none;
  transition: 0.5s;
  font-family:'Dolce Vita Heavy A Few Extra Pounds';
  font-size:calc(4px + 2vmin);
  font-weight: bold;
  float: left;
  
}
.row1{
  width: 100%;
  height: 70px;
  background-color: rgba(178, 34, 34, 0.637);
  float: left;
}
.container1{
  position: relative;
  top: -150px;
  width: 500px;
  /* min-height:600px; */
  /* height: 1000px; */
  /* box-shadow: 10px 10px 5px #333333; */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: rgb(179, 87, 1);
  display: flex;
  flex-direction: column;
  align-items: left;
  /* justify-content: center; */
}
#containword{
  background-color: rgba(178, 34, 34, 0.637);
}
#containword{
  min-height: 100px;
}
#containmean{
  text-align: left;
  font-size: calc(8px + 2vmin); 
  background-color: #703e3e8f;
  transition: 0.5s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
#containmean p{
  margin-left: calc(25px + 2vmin);
  margin-right: 10px;
}
#containmean h5{
  margin-left: 30px;
}
#picContainer{
  width: 100%;
}
#picContainer img{
  width: 60%;
}
#garrulity{
  width:80%;
  max-height:400px;
}
.dispB{
  display: flex;
margin-left: auto;
margin-right: auto;
}
.dispN{
  display: none;
}
#similarWords li{
  list-style-type: none;
}
@media screen and (max-width: 600px)
{
  .container1{
    width: 100vw;
  }

  #addWords textarea{
    width: 80vw;
  }
}